.crud__form {
  position: relative;
}

.form-new-note__header {
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
}

.form-new-note__title {
  font-size: 0.9rem;
}

.form-new-note__input {
  max-width: 550px;
  padding: 0.5rem 2.6rem 0.5rem 0.5rem;
  resize: none;
  outline: none;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.form-new-note__input:focus {
  outline: 2px solid grey;
  outline-offset: -2px;
}

.form-new-note__btn-send {
  position: absolute;
  left: 498px;
  bottom: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border: 1px solid grey;
  border-radius: 50%;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  transition: box-shadow 0.3s;
}

.form-new-note__btn-send:hover {
  box-shadow: 0 0 5px 0 grey;
}
